@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Material+Icons|Material+Icons+Outlined|Material+Icons+Two+Tone|Material+Icons+Round|Material+Icons+Sharp);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{text-align:center;font-family:"Montserrat",sans-serif;margin:auto;width:610px}h1{font-family:"Raleway",sans-serif;font-size:36px}.todo{width:100%;text-align:left;display:-webkit-flex;display:flex;word-break:break-all;position:relative}.todo p{max-width:86%;margin:13px 10px;font-size:18px}.todo *{display:inline-block}.todo__input{margin:15px 0}.todo__add-button{color:#fff;font-size:14px;font-weight:600;background:#2f80ed;height:56px;width:109px;border-radius:12px;border:none}.todo__add-button:hover{background:#1366d6}.todo__add-button:active{background:#0e50a8}.todo__add-input{padding:0 10px;margin-right:20px;height:56px;width:455px;border-radius:12px;border:1px solid #bdbdbd}.todo__add-input::-webkit-input-placeholder{font-size:14px}.todo__add-input:-ms-input-placeholder{font-size:14px}.todo__add-input::placeholder{font-size:14px}.todo__delete{position:absolute;right:0;-webkit-align-self:center;align-self:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;width:24px;height:24px;margin-left:10px;cursor:pointer;color:#828282}.todo__delete:hover{color:#ec8484}.todo-list{list-style:none}.todo-list__delete-all{margin-top:20px;float:right}.todo-list__delete-all button{width:124px;height:40px;border-radius:4px;border:none;font-size:12px;font-weight:600;color:#fff;padding:0;background:#eb5757}.todo-list__delete-all button span{margin-right:5px;position:relative;top:4px;font-size:18px}.todo-list__delete-all button:hover{background:#e62929}.todo-list__delete-all button:active{background:#c51717}.todo-list__delete-all-text{position:relative;top:-4px}.todo--disabled{display:none}.todo--completed p{text-decoration:line-through}.disabled{display:none}.menu{border-bottom:#bdbdbd solid 1px;margin-bottom:10px}.menu ul{list-style:none;display:-webkit-flex;display:flex;margin-bottom:9px;-webkit-justify-content:space-around;justify-content:space-around}.menu li{display:inline-block;cursor:pointer}.menu li::after{top:10px;position:relative;display:block;content:" ";height:6px;width:100px;border-radius:4px 4px 0 0;background:#2f80ed;opacity:0}.menu li:hover::after{opacity:.5}.menu li.menu-active::after{opacity:1}.checkbox-container{-webkit-align-self:center;align-self:center;position:relative;top:-2px;left:3px;height:24px;padding-left:35px;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.checkbox-container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0;top:0;left:0}.checkbox-checkmark{position:absolute;top:0;left:0;height:25px;width:25px;border-radius:4px;border:1px solid #828282}.checkbox-checkmark:after{content:"";position:absolute;display:none}.checkbox-container:hover input~.checkbox-checkmark{background-color:#eee}.checkbox-container input:checked~.checkbox-checkmark{background-color:#2f80ed;border:1px solid #2f80ed}.checkbox-container input:checked~.checkbox-checkmark:after{display:block}.checkbox-container .checkbox-checkmark:after{left:9px;top:5px;width:5px;height:10px;border:solid #fff;border-width:0 3px 3px 0;-webkit-transform:rotate(45deg);transform:rotate(45deg)}
